import { memo } from 'react'
import { LiNav } from 'components/Nav/style'

interface Props {
  subMenu: string[]
}

const SubListNav = ({ subMenu }: Props): JSX.Element => {
  return <ul>
    {
      subMenu.map((submenu: string) => <LiNav key={submenu}>{submenu}</LiNav>)
    }
  </ul>
}

export default memo(SubListNav)